.event-body-ticket {
    display: flex;
    flex-wrap: wrap;
    width: 60vw;
    margin: auto;
    box-shadow: 0 0 5px grey;
    margin-top: 40px;
    margin-bottom: 40px;
    padding-top: 18px;
    padding-bottom: 18px;
 }

 .event-body-ticket-date {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 0 20%;    
 }

 .event-body-ticket-date > h3 {
    color: var(--primary-color);
    margin: auto;
 }

 .event-body-ticket-date > span:nth-child(3){
    color: grey;    
 }

 .event-body-ticket-text {
    display: flex;
    flex: 0 33%;
    flex-direction: column;
 }

 .event-body-ticket-text > h3 {
    margin-bottom: 0;
 }

 .event-body-ticket-text > span{
    color: grey;    
 }

 .event-body-ticket-num {
     display: flex;
     flex: 0 15%;
     align-items: center;
 }

 .event-body-ticket-num > span {
    height: 29px;
    width: 29px;
    cursor: pointer;
    color: grey;
    text-align: center;
 }

 .event-body-ticket-num  input {
    width: 60px;
 }

 .event-body-ticket-num > input {
  padding: 0;
}

 .event-body-ticket-buy {
    display: flex;
    flex: 0 25%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
 }

 .event-body-ticket-buy > span:first-child{ 
    padding-bottom: 4px; 
 }

 .event-body-ticket-buy > button {
    background: var(--primary-color);
    color: white;
    border-radius: 4px;
    padding: 4px 14px;
 }

 .event-body-ticket-buy .button-disabled {
   background: grey;
   color: white;
   border-radius: 4px;
   padding: 4px 14px;
   cursor: not-allowed;
 }

 @media screen and (max-width: 768px) {
    .event-body-ticket {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }

    .event-body-ticket > div {
       margin-bottom: 6px;
    }
  }