.aboutUs-container {
    margin-top: 64px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.aboutUs-img {
    background-image: url(../../assets/images/georgie-cobbs-bKjHgo_Lbpo-unsplash.jpg);
    background-size: cover;
    width: 100%;
    min-height: 400px;
    background-position-y: bottom;
}

.aboutUs {
    max-width: 60%;
    padding-bottom: 20px;
}

.aboutUs-container h3{
    text-align: center;
    padding: 26px 0px;
    margin: 0px;
}

.aboutUs > p {
    color: gray;
}

.about-us-section-wrapper {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.about-us-section-wrapper:nth-child(odd){
    flex-direction: row-reverse;
}

.about-us-img-wrapper,.about-us-text {
    flex: 0 50%;
    min-width: 300px;
}

.about-us-img {
    background-image: url(../../assets/images/jeshoots-com--2vD8lIhdnw-unsplash.jpg);
    background-size: cover;
    height: 100%;
    min-height: 340px;
}

.about-us-img2 {
    background-image: url(../../assets/images/bench-accounting-C3V88BOoRoM-unsplash.jpg);
    background-size: cover;
    height: 100%;
    min-height: 340px;
}

.about-us-img3 {
    background-image: url(../../assets/images/brooke-cagle-uWVWQ8gF8PE-unsplash.jpg);
    background-size: cover;
    background-position-y: center;
    height: 100%;
    min-height: 340px;
}

.about-us-text {
    padding: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.about-us-text > p {
    color: grey;
    max-width: 70ch;
}

@media screen and (max-width: 768px) {
    .aboutUs-img {
        background-position-x: right;
    }

    .aboutUs {
        text-align: center;
    }

    .about-us-img2 {
        background-position-x: right;
    }

    .about-us-text > p {
        text-align: center;
    }
}