.user-container {
    margin-top: 64px;
}

.user-header {
    background: var(--primary-color);
    background: -webkit-linear-gradient(to top, #166b57, var(--primary-color));
    background: linear-gradient(to top, #1b8169, var(--primary-color));
    width: 100%;
    height: 200px;
    justify-content: space-around;
    display: flex;
}

.user-header-left {
    display: flex;
    align-items: flex-end;
    flex: 0 20%;
    margin-top: 24px;
    padding: 24px;
    background-color: rgba(223, 223, 223, 0.3);
}

.user-header-left-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.user-header-left-text > span {
    font-size: 18px;
    color: white;
}

.user-header-left-text>img {
    width: 50px;
    padding-bottom: 6px;
}

.user-header-right {
    flex: 0 60%;
    margin-top: 24px;
    display: flex;
    align-items: center;
}

.user-body {
    display: flex;
    justify-content: space-around;
    width: 100%;
    min-height: 60vh;
    background-color: rgb(240, 240, 240);
}

.user-body-left-container {
    display: flex;
    flex: 0 20%;
    box-shadow: 0px 1px 7px grey;
    padding: 26px 11px;
}

.user-body-left {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.user-body-left button {
    position: relative;
    padding: 12px 24px;
    display: flex;
    column-gap: 8px;
}

.user-body-left button::after {
    content: "";
    position: absolute;
    height: 1px;
    width: 80%;
    display: block;
    left: 24px;
    top: 45px;
    background-color: gray;
    transition: all 0.2s;
}

.user-body-left button:hover::after {
    height: 2px;
}

.user-body-left span {
    color: rgb(78, 78, 78);
    cursor: pointer;
}

.user-body-right {
    display: flex;
    flex: 0 60%;
    padding: 32px 0px;
    flex-direction: column;
    align-items: center;
}

@media screen and (max-width: 768px) {
    .user-body {
        flex-direction: column;
    }
    .user-header {
        flex-wrap: wrap;
        min-height: 330px;
    }
    .user-header-right {
        flex: 0 100%;
        justify-content: center;
    }
    .user-header-left {
        flex: 0 100%;
        align-items: center;
        margin: 0;
        padding: 0;
    }
}