            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 22rem;
    height: 26rem;
    margin-left: 10rem;
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2);
    border-radius: 5px;
    background-color: rgba(255, 255, 255, .15);
}

.login-form {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
}

.login-form > img {
    width: 33%;
    padding-bottom: 40px;
}

.login-form .error-login {
    border: 1px solid red;
    border-radius: 4px;
    width: 62%;
    padding: 8px;
    margin-bottom: 10px;
    background: rgba(255, 0, 0,0.1);
}

.login-form > input{
    height: 40px;
    border-radius: 60px;
    border: 1px solid #868686;
    padding-left: 16px;
    font-size: 18px;
    transition: all 0.5s;
    margin-bottom: 16px;
}

.input-password {
    height: 40px;
    width: 234px;
    border-radius: 60px;
    border: 1px solid #868686;
    transition: all 0.5s;
    margin-bottom: 16px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    background: white;
}

.input-password > input {
    outline: none;
    border: 0;
    width: 85%;
    padding-left: 16px;
    font-size: 18px;
    border-radius: 60px;
}

.login-form .error {
    border: 2px solid red;
}

.login-form > input:focus{
    outline: none;
}

.login-form button{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 67%;
    height: 40px;
    border-radius: 60px;
    font-size: 18px;
    background-color: var(--primary-color);
    border-color: transparent;
    color: #fff;
    transition: all 0.5s;
    outline: none;
}

.login-form button:hover{
    background-color: var(--secondary-color);
}

.login-form button:active{
    background-color: white;
    color: black;
    border: 1px solid var(--primary-color);
    box-shadow: none;
}

