.card-concert {
    width: 518px;
    height: 280px;
    margin: 20px 10px;
    position: relative;
    background-color: transparent;
}

.card-concert::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.25);
    height: 100%;
    width: 100%;
    transition: all .5s;
    pointer-events: none;
    border-radius: 6px;
}

.card-concert:hover::after{
    background-color: rgba(0,0,0,1);
}

.card-concert:hover .card-concert-text {
    opacity: 1;
}

.card-concert  img {
    width: 100%;
    height: 100%;
    border-radius: 6px;
}

.card-concert-text {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    position: absolute;
    -webkit-justify-content: center;
            justify-content: center;
    z-index: 3;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    opacity: 0;
    transition: all .5s .5s;
    cursor: pointer;
    pointer-events: none;
}

.card-concert-text > p{
    color: white;
    margin-left: 6px;
}

@media screen and (max-width: 768px) {
    .card-concert {
        width: 100%;
        height: 198px;
    }
}
